.animate-pulse-glow {
  position: relative;
}
.animate-pulse-glow::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  animation: glow-wide 7s ease-in-out infinite;
  pointer-events: none;
  z-index: -1;
}
.animate-pulse-glow::after {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  animation: glow-tight 4s ease-in-out infinite;
  pointer-events: none;
  z-index: -1;
}
