.recommended-products-grid {
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  column-count: 6;
  column-gap: 16px;
  column-fill: balance;
  transition: column-gap 0.3s ease;
}
.recommended-products-grid-item {
  break-inside: avoid;
  margin-bottom: 16px;
  transition: transform 0.25s ease, opacity 0.3s ease;
}
.recommended-products-grid-item:hover .recommended-product-item {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}
@media (max-width: 1200px) {
  .recommended-products-grid {
    column-count: 5;
  }
}
@media (max-width: 960px) {
  .recommended-products-grid {
    column-count: 4;
  }
}
@media (max-width: 720px) {
  .recommended-products-grid {
    column-count: 3;
  }
}
@media (max-width: 520px) {
  .recommended-products-grid {
    column-count: 2;
    column-gap: 16px;
  }
}
.quick-start-item {
  width: calc((100vw - 2rem) / 2.5);
  min-width: calc((100vw - 2rem) / 2.5);
  height: calc((100vw - 2rem) / 2.5);
  aspect-ratio: 1/1;
}
@media (min-width: 640px) {
  .quick-start-item {
    width: 148px;
    min-width: 148px;
    height: 148px;
  }
}
.video-section-height {
  height: 350px;
}
@media (min-width: 640px) {
  .video-section-height {
    height: 700px;
  }
}
.quick-start-scroll-wrap {
  width: 100%;
}
@media (max-width: 639px) {
  .quick-start-scroll-wrap {
    margin-left: -24px;
    margin-right: -24px;
    width: calc(100% + 48px);
  }
  .quick-start-scroll-inner {
    padding-left: 24px;
    padding-right: 24px;
  }
}
